.header__title-button {
  font-family: 'Inter', Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: 0em;
  height: 22px;
  text-align: center;
  text-decoration: none;
  color: #fff;
  margin: 0 25px 0 0;
}

.header__title-button:hover {
  opacity: 0.6;
}

@media (min-width: 500px) {
  .header__title-button {
    font-size: 18px;
  }
}
