.header__burger-button {
  font-family: 'Inter', Arial, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: right;
  background-color: rgba(0, 0, 0, 0);
  color: #a9a9a9;
  height: 22px;
  cursor: pointer;
  visibility: hidden;
  border: none;
}

.header__burger-button:hover {
  opacity: 0.6;
}

@media (min-width: 500px) {
  .header__burger-button {
    visibility: inherit;
  }
}
